import nscCore from '@/lib/core';
import nscExt from '@/lib/extension';
import LS from '@/lib/LocalStorage';
import NimbusConnect from '@/lib/NimbusConnect';
import Screencast from '@/lib/screencast';

export default class AfterCapture {
  static async run(type, action = null) {
    let actionQuery = `${typeof action === 'string' ? `?action=${action}` : ''}`;
    let enableEdit = await LS.getItem('enableEdit');

    console.log('AfterCapture', type, action, enableEdit);
    if (type === 'screencast') {
      enableEdit = 'edit';
    }

    // eslint-disable-next-line default-case
    switch (action) {
      case 'nimbus':
      case 'google':
      case 'dropbox':
      case 'slack':
      case 'quick':
      case 'print':
      case 'pdf':
        return this.handleOpenUploadPage(type, actionQuery);
      case 'edit':
        actionQuery = `?action=${enableEdit}`;
        break;
      case 'download':
        return this.handleSaveFile(type);
      case 'blank':
        return nscExt.openEditPage(`${type}${actionQuery}`);
    }

    actionQuery = `${typeof enableEdit === 'string' ? `?action=${enableEdit}` : ''}`;

    // eslint-disable-next-line default-case
    switch (enableEdit) {
      case 'nimbus':
      case 'google':
      case 'dropbox':
      case 'done':
        return this.handleOpenUploadPage(type, actionQuery);
      case 'edit':
        return nscExt.openEditPage(`${type}${actionQuery}`);
      case 'save':
        return this.handleSaveFile(type);
    }

    return false;
  }

  static handleOpenUploadPage(type, actionQuery = '') {
    console.log('handleOpenUploadPage', type.replace('-editor', ''));
    return nscExt.openEditPage(`${type.replace('-editor', '')}${actionQuery}`);
  }

  static async handleSaveFile(type) {
    console.log('handleSaveFile', type);
    // const file = '';

    if (type === 'screenshot' || type === 'screenshot-editor') {
      const parts = await nscExt.imageFileParts();
      const info = await nscExt.imageFileInfo();
      const format = await nscExt.imageFormat();

      const response = await nscCore.sendTabMessage(null, {
        operation: 'getScreenshotDetails',
        info,
        format,
        parts
      });

      if (response) {
        return this.downloadFile({ url: response.url, fileName: response.fileName }, true);
      //   const blob = await fetch(response.url).then(r => r.blob());
      //
      //   file = {
      //     url: response.url,
      //     blob,
      //     size: blob.size,
      //     name: response.fileName,
      //     comment: ''
      //   };
      }
    }

    // if (!file) {
    return false;
    // }
  }

  static async downloadFile({ url, fileName }, isAuto = false, window = null) {
    console.log('downloadFile', url, fileName);
    if (!url || !fileName) {
      return;
    }

    if (window) {
      window.onbeforeunload = () => {};
    }

    if (isAuto) {
      await chrome.downloads.download({ url, filename: fileName, saveAs: true });
      if (window) {
        window.close();
      }
    } else {
      const saveAs = await nscExt.enableSaveAs();
      await chrome.downloads.download({ url, filename: fileName, saveAs });
    }
  }

  static async saveGuide() {
    nscCore.syncSendAllTabMessage({ operation: 'content_automation_status_upload' });

    const rootDir = await navigator.storage.getDirectory();
    const fileHandleOrigin = await rootDir.getFileHandle('video.webm', { create: true });
    const file = await fileHandleOrigin.getFile();
    const buffer = await file.arrayBuffer();

    const blobVideo = new Blob([buffer], {
      type: 'video/webm'
    });
    const name = nscExt.getFileNameVideo('webm');
    const fileUpload = [];
    const imageUpload = [];

    const events = structuredClone(Screencast.listVideoGuideEvents);
    Screencast.listVideoGuideEvents = [];

    console.log('saveGuide all events', events);

    if (blobVideo.size > 0) {
      const { files } = await NimbusConnect.fileUpload({ blob: blobVideo, name });
      console.log('videoUpload', blobVideo, files);
      fileUpload.push({ attachName: files.file, type: 'video', attachId: nscExt.generateGUID(16) });
    }

    // eslint-disable-next-line no-unused-vars,no-restricted-syntax
    for await (const [_index, { blob }] of events.entries()) {
      const { files } = await NimbusConnect.fileUpload({ blob });
      console.log('imageUpload', blob, files);
      imageUpload.push({ attachName: files.file, type: 'image', attachId: nscExt.generateGUID(16) });
    }

    // eslint-disable-next-line camelcase
    const { body: { user_id } } = await NimbusConnect.getInfo();
    const personalOrganization = await NimbusConnect.getPersonalOrganizations(user_id);
    const { body: { workspaces } } = await NimbusConnect.getWorkspaces({ orgId: personalOrganization.id });
    const [workspaceDefault] = workspaces.filter(work => work.isDefault);
    console.log(personalOrganization, workspaceDefault);

    const noteId = nscExt.generateGUID(16);
    // const parentId = await LS.getItem('nimbusFolderSelect');
    const { url, title, favIconUrl } = await LS.getItem('pageInfo');
    const titleGuide = `Step by Step Guide for ${title}`;

    await NimbusConnect.notesCreate({
      noteId,
      workspaceId: workspaceDefault.globalId,
      parentId: 'default',
      pageUrl: url,
      attachements: [...fileUpload, ...imageUpload],
      title: titleGuide,
      role: 'guide'
    });
    console.log('notesCreate', noteId, title, [...fileUpload, ...imageUpload]);

    // eslint-disable-next-line no-restricted-syntax,no-unused-vars
    for await (const [_index, { attachId }] of fileUpload.entries()) {
      await NimbusConnect.notesAddFile({
        noteId, workspaceId: workspaceDefault.globalId, attachId, type: 'file', insertAfter: 'first'
      });
      console.log('notesAddVideo', attachId);
    }

    await NimbusConnect.notesAddBookmark({
      noteId, workspaceId: workspaceDefault.globalId, src: url, icon: favIconUrl, name: title, description: title
    });
    console.log('notesAddBookmark', url);

    // eslint-disable-next-line no-restricted-syntax
    for await (const [index, { attachId }] of imageUpload.entries()) {
      const {
        element,
        value,
        event,
        label
      } = events[index];
      let text = '';
      const clearValue = (value && value.replace(/\s+|\n+/g, ' ').slice(0, 50)) || '';
      if (event === 'click') {
        if (element === 'input') {
          text = `Click on the field ${label || clearValue}`;
        } else {
          text = `Click on the ${label || clearValue}`;
        }
      } else if (event === 'mouseup') {
        text = `Drag highlighted item ${clearValue}`;
      } else if (element === 'input') {
        text = `Write to ${label} "${clearValue}"`;
      } else if (element === 'select') {
        text = `Select ${label} ${clearValue}`;
      } else if (element === 'radio') {
        text = `Select ${label}`;
      } else if (element === 'checkbox') {
        text = `Toggle Switch ${label}`;
      } else {
        text = `Click on the ${clearValue || label}`;
      }

      await NimbusConnect.notesAddStep({
        noteId, workspaceId: workspaceDefault.globalId, text, attachId
      });

      console.log('notesAddStep', text);

      // await NimbusConnect.notesAddHint({
      //   noteId, workspaceId: workspace.globalId, text
      // });
      // console.log('notesAddHint', text);
      //
      // await NimbusConnect.notesAddFile({
      //   noteId, workspaceId: workspace.globalId, attachId, type: 'image', insertAfter: 'last'
      // });
      // console.log('notesAddImage', attachId);
    }

    nscCore.syncSendAllTabMessage({ operation: 'content_automation_status_upload_end' });

    const recordGuideCountCreate = await LS.getItem('recordGuideCountCreate');
    await LS.setItem('recordGuideCountCreate', recordGuideCountCreate + 1);

    const domain = (personalOrganization.domain ? `https://${personalOrganization.domain}` : `https://${personalOrganization.sub}.nimbusweb.me`);
    const urlNote = `${domain}/ws/${workspaceDefault.globalId}/recent/note/${noteId}`;
    console.log('personalOrganization domain sub', domain, personalOrganization.sub);
    console.log('workspace id, node id', workspaceDefault.globalId, noteId);

    await nscExt.openPage(urlNote);
  }
}
