import Screencast from '@/lib/screencast';
import nscExt from '@/lib/extension';

(async function core() {
  const sendMessage = async data => new Promise(resolve => {
    chrome.runtime.sendMessage(data, resolve);
  });

  let isRecord = false;
  let audioStream = null;
  let videoStream = null;
  let mediaRecorder = null;
  let timeStart = null;
  let timePause = null;
  let stopStreamCrop = () => {};
  let mediaRecorderChunks = [];
  const IsVideoStream = false;

  const getState = () => {
    if (timeStart) {
      return !timePause ? 'recording' : 'paused';
    }
    return 'inactive';
  };

  const getStatus = () => !!timeStart;

  const getTime = () => {
    const date = Date.now();

    timeStart += (timePause ? date - timePause : 0);
    timePause = timePause ? date : null;
    return timeStart ? date - timeStart : 0;
  };

  const screencastUpdateStatus = async () => {
    const { videoRecordType } = await sendMessage({ operation: 'getDataStartScreencast' });
    if (videoRecordType !== 'camera') {
      await sendMessage({ operation: 'screencastUpdateStatus', status: getStatus(), state: getState() });
    }
  };

  const stopMediaRecorder = async () => {
    if (!isRecord) {
      return;
    }
    isRecord = false;

    await sendMessage({ operation: 'screencastUpdateBrowserAction', action: 'default' });

    stopStreamCrop();

    if (audioStream?.active) {
      audioStream.getTracks()
        .forEach(track => {
          track.stop();
        });
    }

    if (videoStream?.active) {
      videoStream.getTracks()
        .forEach(track => {
          track.stop();
        });
    }

    const blob = new Blob(mediaRecorderChunks, { type: 'video/webm' });
    const rootDir = await navigator.storage.getDirectory();
    console.log(blob);
    await sendMessage({ operation: 'screencastSetDefaultVideoName' });

    const fileHandle = await rootDir.getFileHandle('video.webm', { create: true });
    const writable = await fileHandle.createWritable();
    await writable.write(blob);
    await writable.close();

    console.log('content written to file', blob);

    audioStream = null;
    videoStream = null;
    mediaRecorder = null;
    timeStart = null;
    timePause = null;
    mediaRecorderChunks = [];

    await screencastUpdateStatus();

    await sendMessage({ operation: 'screencastAfterStop' });
  };

  const startMediaRecorder = async () => {
    const {
      videoTabSoundEnable,
      videoMicSoundEnable,
      selectedMicrophone,
      videoRecordType,
      videoResolution,
      getTrial,
      trialVideoDuration,
      isNimbusPremium,
      videoCropPosition
    } = await sendMessage({ operation: 'getDataStartScreencast' });
    let constraints = {};
    console.log({
      videoTabSoundEnable,
      videoMicSoundEnable,
      selectedMicrophone,
      videoRecordType,
      videoResolution,
      getTrial,
      trialVideoDuration,
      isNimbusPremium,
      videoCropPosition
    });

    if (videoRecordType === 'tab' || videoRecordType === 'tab-select') {
      const { streamId } = await sendMessage({ operation: 'getTabStreamId' });

      constraints = {
        audio: false,
        video: {
          mandatory: {
            chromeMediaSource: 'tab',
            chromeMediaSourceId: streamId,
            maxFrameRate: 30,
            maxWidth: videoResolution.width,
            maxHeight: videoResolution.height
          }
        }
      };

      if (videoTabSoundEnable) {
        constraints.audio = {
          mandatory: {
            chromeMediaSource: 'tab',
            chromeMediaSourceId: streamId
          }
        };
      }
    } else {
      const { streamId, options } = await sendMessage({ operation: 'getDesktopStreamId' });
      console.log(streamId, options);

      constraints = {
        video: {
          mandatory: {
            // chromeMediaSource: 'tab',
            chromeMediaSource: 'desktop',
            chromeMediaSourceId: streamId,
            maxFrameRate: 30,
            maxWidth: videoResolution.width,
            maxHeight: videoResolution.height
          }
        }
      };

      if (options.canRequestAudioTrack) {
        constraints.audio = {
          mandatory: {
            // chromeMediaSource: 'tab',
            chromeMediaSource: 'desktop',
            chromeMediaSourceId: streamId
          }
        };
      }
      console.log(constraints);
    }

    try {
      videoStream = await Screencast.getUserMedia(constraints);

      if (videoTabSoundEnable) {
        const ctx = new AudioContext({ latencyHint: 0.03 });
        const output = ctx.createMediaStreamSource(videoStream);
        output.connect(ctx.destination);
      }
    } catch (err) {
      console.error(`Error video: ${err}`, err);
    }

    if (!videoStream) {
      return;
    }

    if (videoMicSoundEnable) {
      let constraintsAudio = { audio: true };

      if (selectedMicrophone !== 'default') {
        constraintsAudio = {
          audio: {
            deviceId: {
              ideal: selectedMicrophone
            }
          }
        };
      }

      try {
        audioStream = await Screencast.getUserMedia(constraintsAudio);
      } catch (err) {
        console.error(`Error audio: ${err}`, err);
      }

      if (audioStream) {
        const mixedStream = new MediaStream();
        const mixedAudioStream = await Screencast.getMixedAudioStream([audioStream, videoStream]);
        mixedAudioStream.getTracks().forEach(track => mixedStream.addTrack(track));
        videoStream.getTracks().forEach(track => mixedStream.addTrack(track));
        videoStream = mixedStream;
      }
    }

    try {
      if (videoRecordType === 'tab-select') {
        const { outputStream, stop } = await nscExt.streamCrop(videoStream, videoCropPosition);
        videoStream = outputStream;
        stopStreamCrop = stop;
      }
    } catch (err) {
      console.error(`Error crop video: ${err}`, err);
    }

    videoStream.getVideoTracks()
      .forEach(track => {
        track.onended = stopMediaRecorder;
      });

    mediaRecorder = new MediaRecorder(videoStream, { mimeType: 'video/webm; codecs=vp8' });
    timeStart = Date.now();

    if (IsVideoStream) {
      await sendMessage({ operation: 'screencastUpdateBrowserAction', action: 'loading' });
    } else {
      await sendMessage({ operation: 'screencastUpdateBrowserAction', action: 'rec' });
    }

    mediaRecorder.ondataavailable = async e => {
      mediaRecorderChunks.push(e.data);

      if ((IsVideoStream || getTrial) && !isNimbusPremium && Date.now() - timeStart > trialVideoDuration) {
        await stopMediaRecorder();
      }
    };

    mediaRecorder.onstop = stopMediaRecorder;
    mediaRecorder.start(1000);

    isRecord = true;
    await screencastUpdateStatus();
  };

  chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    // console.log(request.operation);
    switch (request.operation) {
      case 'startScreencast':
        (async function startScreencast() {
          const {
            videoRecordIsGuide,
            videoRecordGuide
          } = await sendMessage({ operation: 'getDataStartScreencast', action: 'init' });

          if (videoRecordIsGuide) {
            if (videoRecordGuide) {
              await startMediaRecorder();
            } else {
              timeStart = Date.now();
              isRecord = true;
              await sendMessage({ operation: 'screencastUpdateBrowserAction', action: 'rec' });
              await screencastUpdateStatus();
            }
          } else {
            await startMediaRecorder();
          }
        }());
        break;
      case 'stopScreencast':
        (async function stopScreencast() {
          const {
            videoRecordIsGuide
          } = await sendMessage({ operation: 'getDataStartScreencast' });

          if (mediaRecorder) {
            mediaRecorder.stop();

            if (videoStream) {
              videoStream.getTracks()
                .forEach(track => {
                  track.stop();
                });
            }
          } else if (videoRecordIsGuide) {
            await stopMediaRecorder();
          }
        }());
        break;
      case 'pauseScreencast':
        (async function pauseScreencast() {
          if (getState() === 'recording') {
            timePause = Date.now();
            if (mediaRecorder) {
              mediaRecorder.pause();
            }
            await sendMessage({ operation: 'screencastUpdateBrowserAction', action: 'pause' });
          } else {
            timePause = null;
            if (mediaRecorder) {
              mediaRecorder.resume();
            }
            await sendMessage({ operation: 'screencastUpdateBrowserAction', action: 'rec' });
          }

          await screencastUpdateStatus();
        }());
        break;
      case 'getScreencastStatusOffscreen': {
        const data = {
          time: getTime(),
          status: isRecord,
          state: getState()
        };
        // console.log('getScreencastStatusOffscreen', data);
        sendResponse(data);
        break;
      }
      default:
        break;
    }

    return true;
  });
}());
